.category-wrapper {
  margin-bottom: 2em;
  text-align: -webkit-center;
}

.category {
  max-width:375px;
  height:275px;
  background-size: cover !important;
  background-position: center center !important;
  position: relative;
}

.category:hover {
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
}

.categoryText {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  width: 100%;
  height: 45px;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  bottom: 0;
  color: #fff;
  text-align: center;
  margin: 0;
  padding-top: 12px;
  box-sizing: border-box
}

.categoryText h2 {
  margin: 0;
  padding: 0;
}

.categoryText h2::first-letter {
  text-transform: uppercase !important;
}
html, body {
  margin: 0 !important;
  background: rgba(247, 206, 69, 0.67);
  min-height: 100vh !important;
}

.wrapper {
  max-width: 90%;
  margin: 0 auto;
  padding-bottom: 100px;
  padding-top: 30px;
  box-sizing: border-box;
}

.header {
  display: flex;
  padding: 10px;
  box-sizing: border-box;
  margin:0;
  height: 85px;
  position: relative;
  margin-bottom: 10px;
}

.logo {
  width: 135px;
  height: 63px;
  background-repeat: no-repeat !important;
  background-position: center center !important;
  background-size: 90% !important;
  box-sizing: border-box;
}

.header-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 240px;
  box-sizing: border-box;
  text-align: right;
  font-family: Arial, Helvetica, sans-serif;
  padding-right: 10px;
}

.header-text h2 {
  line-height: 0;
}

.header-text p {
  line-height: 5px;
}

.content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.nav-item {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #000;
}

.nav-item a {
  text-decoration: none;
  color: #000;
  text-transform: capitalize;
}

.nav-divider {
  padding: 0 5px 0 5px;
}
.single-element-wrapper {
  display: flex;
}

.single-element-info div {
  display: flex;
}

.single-element-image {
  display: block;
  position: relative;
  width: 45%;
  height: 500px;
  background-size: cover !important;
  background-position: center center !important;
  margin-right: 20px;
}

.single-element-info {
  display: block;
  background: #000;
  height: auto;
  min-height: 200px;
  width: 50%;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  padding: 20px 10px;
  box-sizing: border-box;
  line-height:20px;
}

.info-property {
  font-weight: bold;
  font-size: 15px;
  line-height: 23px;
  text-transform: uppercase;
  color:#939393;
  margin-right: 10px;
}

.left {
  flex: 50% 1;
}

.right {
  flex: 50% 1;
}
.grid-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  justify-content: space-between;
  margin: 0 auto;
}

.grid-element {
  margin: 10px 5px;
  background:#000;
  min-width: 172px;
  min-height: 279px;
  box-sizing: border-box;
  position:relative;
}

.grid-element:hover {
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
}

.grid-element-upper {
  min-height:237px;
  background-size: cover !important;
  background-position: center center !important;
}

.grid-element-lower {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  min-height:42px;
  background: #000;
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  padding-top: 2px;
  white-space: nowrap;
}

.loader {
  border: 5px solid #f3f3f3; 
  border-top: 5px solid #E4D01C; 
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 20px auto;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.load-more-button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 800;
  display: block;
  width: 100%;
  height: 50px;
  text-align: center;
  background: #000;
  cursor: pointer;
  margin: 0 auto;
  width: 355px;
  position: relative;
  box-sizing: border-box;
  transition: 0.3s;
}

.load-more-button p {
  padding-top: 10px;
  color: #fff;
}

.load-more-button:hover {
  background: #fff;
}

.load-more-button:hover p {
  color: #000;
}
.related-header-wrapper {
  display: block;
  width: 100%;
  height: 50px;
  background: #000;
  position: relative;
  bottom: 0px;
  color:#fff;
  margin: 0 auto;
  box-sizing: border-box;
}

.related-header-wrapper h2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 800;
  padding:10px 0 0 10px;
}
.searchform-wrapper {
  display: block;
  width: 100%;
  height: 50px;
  background: #000;
  position: relative;
  bottom: 0px;
  color:#fff;
  font-family: Arial, Helvetica, sans-serif;
}

input[type=text] {
  width:70%;
  height:30px;
  margin: 8px 0 0 10px;
  border: 0px;
  background: #000;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 16px;
  outline: none;
}
.wrapper-category {
  max-width: 90%;
  margin: 0 auto;
  padding: 0 10px 100px 10px;
  box-sizing: border-box;
}

.category-logo {
  width: 80px;
  height: 37px;
  background-repeat: no-repeat !important;
  background-position: center center !important;
  background-size: 90% !important;
  box-sizing: border-box;
  position: absolute;
  top: 15px;
  left: 0px;
  padding: 0;
  margin: 0;
}

.header-category {
  display: flex;
  padding: 10px 0 10px 0;
  box-sizing: border-box;
  margin:0;
  height: 100px;
  position: relative;
}

.header-navigation {
  position: absolute;
  bottom: 0px;
  left: 0px;
  padding: 0;
  margin-bottom: 10px;
  height: 20px;
}

